.team {
  width: 100%;
  height: 914px;
  background-color: #e7f1f8;

  .col-12 {
    width: 1140px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: auto;
    h1 {
      text-align: center;
      font-family: titillium_webregular, sans-serif;
      font-size: 37px;
      line-height: 28px;
      font-weight: bold;
      letter-spacing: 5.6px;
      margin-left: 4px;
      color: #3c4761;
      padding-top: 119px;
    }
    .line {
      display: flex;
      justify-content: center;
      margin-top: 13px;
      img {
        opacity: 0.14;
        margin-top: 3px;
      }
    }
    p{
      font-family: titillium_webregular, sans-serif;
      font-size: 18.6px;
      line-height: 26px;
      font-weight: 400;
      letter-spacing: 0.1px;
      color: #3c4761;
      text-align: center;
      margin-top: 19px;
    }
  }
  .icon-wrapper{
    width: 1140px;
    display: flex;
    margin: auto;
    padding-top: 61px;
    .col-3{
      display: flex;
      flex-direction: column;
      align-content: center;
    }
    span{
      font-family: titillium_webregular, sans-serif;
      font-size: 21px;
      line-height: 18px;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 2.5px;
      color: #3c4761;
      margin-top: 40px;
    }
    h3{
      font-family: titillium_webregular, sans-serif;
      font-size: 16px;
      line-height: 18px;
      font-weight: 400;
      letter-spacing: 0.02em;
      color: #30bae7;
      text-align: center;
    }
    p{
      font-family: titillium_webregular, sans-serif;
      font-size: 16px;
      line-height: 24px;
      font-weight: 300;
      letter-spacing: 0.02em;
      color: #3c4761;
      text-align: center;
    }
    .col-3:nth-child(1){
      img{
        margin-left: 35px;
      }
      span{
        margin-left: 38px;
      }
      h3{
        margin-top: 12px;
        margin-left: 15px;
        letter-spacing: 0.5px;
      }
      p{
        letter-spacing: 0.2px;
        padding-left: 32px;
        padding-right: 17px;
        margin-top:13px;
      }
      .social-links{
        margin-left: 48px;
        margin-top: 13px;
        img{
          margin-left: 7px;
        }
      }
    }
    .col-3:nth-child(2){
      img{
        margin-left: 30px;
      }
      span{
        letter-spacing: 2.4px;
        margin-left: 61px;
      }
      h3{
        letter-spacing: 0.5px;
        margin-left: 5px;
        margin-top: 14px;
      }
      p{
        margin-top: 13px;
        padding-left: 27px;
        padding-right: 22px;
        letter-spacing: 0.2px;
      }
      .social-links{
        margin-left: 64px;
        margin-top: 11px;
        img{
          margin-left: 7px;
        }
      }
    }
    .col-3:nth-child(3){
      img{
        margin-left: 25px;
      }
      span{
        margin-left: 51px;
        letter-spacing: 2.1px;
      }
      h3{
        margin-top: 14px;
        margin-right: 6px;
        letter-spacing: 0.3px;
      }
      p{
        margin-top: 13px;
        padding-left: 21px;
        padding-right: 24px;
      }
      .social-links{
        margin-left: 37px;
        margin-top: 11px;
        img{
          margin-left: 7px;
        }
      }
    }
    .col-3:nth-child(4){
      img{
        margin-left: 20px;
      }
      span{
        margin-left: 37px;
        letter-spacing: 2px;
      }
      h3{
        margin-top: 13px;
        margin-right: 3px;
        letter-spacing: 0.5px;
      }
      p{
        margin-top: 14px;
        padding-left: 28px;
        padding-right: 32px;
      }
      .social-links{
        margin-left: 53px;
        margin-top: 11px;
        img{
          margin-left: 7px;
        }
      }
    }
  }
}
